@use '../foundation' as *;

.p-after {
}

.p-after__inner {
    // padding-bottom: rem(80);
    // @include mq(md) {
    //     padding-bottom: rem(120);
    // }
}

.p-after__wrap {
}

.p-after__title {
    margin-bottom: rem(24);
}



.p-after__content + .p-after__content {
    background-color: $color-background;
    width: 100vw;
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
}

.p-after__content{
  padding-bottom: rem(60);
    padding-top: rem(60);
}

.p-after__content-inner {
    max-width: 100%;
    @include mq(md) {
        max-width: rem(1280);
        margin: 0 auto;
    }
}

.p-after-list__item {
    @include mq(md) {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.p-after-list__item:nth-of-type(even) {
    @include mq(md) {
        flex-direction: row-reverse;
    }
}

.p-after-list__item {
    &:not(:last-child) {
        margin-bottom: rem(120);
    }
}

.p-after-list__item-img {
    width: 100%;
    @include mq(md) {
        max-width: rem(600);
    }
}

.p-after-list__item:nth-of-type(even) .p-after-list__item-img {
    @include mq(md) {
        margin-left: rem(100);
    }
}

.p-after-list__item-img img {
    width: 100%;
    aspect-ratio: 680/390;
    object-fit: cover;
}

.p-after-list__content {
    @include mq(md) {
        margin-top: 0;
        margin-left: rem(45);
        max-width: rem(680);
        width: 100%;
    }
}

.p-after-list__item-title {
    font-size: rem(30);
    font-weight: bold;
    margin-bottom: rem(20);
    @include mq(md) {
        font-size: rem(50);
    }
}

.p-after-list__item-text {
    font-size: rem(20);
    font-weight: bold;
    line-height: 1.6;
    letter-spacing: 0.05em;
    @include mq(md) {
        font-size: rem(24);
    }
}

.p-after-list__item:nth-of-type(even) .p-after-list__item-text {
    @include mq(md) {
        margin-left: 0;
    }
}
